import React, { useContext } from "react";
import { Alert } from "react-bootstrap";
import { ErrorsContext } from "../../App";
import "./ErrorsAlerts.css";

const ErrorsAlerts = () => {
  const {
    errorsState: { errors },
    errorsDispatch,
  } = useContext(ErrorsContext);

  const closeError = () => {
    errorsDispatch({ type: "cleanErrors" });
  };
  return (
    <div className="ErrorsAlerts">
      {errors.length > 0 && (
        <Alert variant="danger" onClose={closeError} dismissible>
          <Alert.Heading>Ups! Something went wrong!</Alert.Heading>
          {errors.map((error: string) => (
            <p key={error}>{`â€” ${error}`}</p>
          ))}
          Contact us via <Alert.Link href="#">Telegram</Alert.Link>
        </Alert>
      )}
    </div>
  );
};

export default ErrorsAlerts;
