import CryptoJS from "crypto-js";
import Bottleneck from "bottleneck";

const limiter = new Bottleneck({
  reservoir: 15,
  reservoirRefreshAmount: 15,
  reservoirRefreshInterval: 1000,
});

export function getSig(path: string, secret: string) {
  return CryptoJS.algo.HMAC.create(CryptoJS.algo.SHA256, secret)
    .update(path)
    .finalize()
    .toString(CryptoJS.enc.Hex);
}
export const callApi = (method: string, path: string) => {
  const API_KEY =
    new URLSearchParams(window.location.search).get("api_key") || "";
  const SECRET =
    new URLSearchParams(window.location.search).get("secret") || "";

  return limiter.schedule(() =>
    fetch(`https://api.3commas.io${path}`, {
      method: method,
      headers: {
        APIKEY: API_KEY,
        Signature: getSig(path, SECRET),
      },
    })
  );
};

export const getBots = async (
  offset: number,
  limit: number,
  sorting: string,
  scope: string,
  type: string,
  accountId: string,
  resetOffset = false
) => {
  const getBotsUrl = () =>
    `/public/api/ver1/bots?offset=${
      resetOffset ? 0 : offset
    }&limit=${limit}&sort_by=${sorting.split("-")[0]}&sort_direction=${
      sorting.split("-")[1]
    }${scope ? `&scope=${scope}` : ""}${type ? `&strategy=${type}` : ""}${
      accountId ? `&account_id=${accountId}` : ""
    }`;

  return callApi("GET", getBotsUrl()).then((response) => response.json());
};

export const getAccounts = async () => {
  return callApi("GET", `/public/api/ver1/accounts`).then((response) =>
    response.json()
  );
};
export const getStrategies = async () => {
  return callApi("GET", `/public/api/ver1/marketplace/items?&limit=1000`).then(
    (response) => response.json()
  );
};
