import React, { useContext, useEffect, useState } from "react";
import { callApi } from "../../../utils/ApiUtils";
import { Table } from "react-bootstrap";
import { Badge } from "react-bootstrap";
import { Button } from "react-bootstrap";
import "./BotPreview.css";
import {
  getFundsLocked,
  getMaxUsage,
  getReturns,
  differenceInDays,
} from "../../../utils/BotsUtils";
import { ErrorsContext, StrategiesContext } from "../../../App";

const strategyMap: any = {
  nonstop: "Non-stop",
  qfl: "QFL",
  tv_custom_signal: "TV custom signal",
  trading_view: "TradingView Screener",
  rsi: "RSI-3m-20",
  manual: "Manual",
  ult: "ULT",
  ta_preset: "TA Preset",
  cqs_telegram: "CQS Scalping",
  krypto_boss_free: "HIRN Crypto Free",
};

const getBotsStrategyName = (
  strategiesState: any,
  constantStrategiesMap: any,
  botsStrategiesList: any
) => {
  if (botsStrategiesList.length > 1) {
    return "Combined";
  }

  const botsStrategy = botsStrategiesList[0].strategy;

  if (botsStrategy in constantStrategiesMap) {
    return constantStrategiesMap[botsStrategy];
  }

  const marketsStrategy = strategiesState.strategies.find(
    (marketStrategy: any) => marketStrategy.strategy_key === botsStrategy
  );

  if (marketsStrategy) {
    return marketsStrategy.name;
  }

  return botsStrategy;
};

export const BotPreview = ({ botItem, account }: any) => {
  const { errorsDispatch } = useContext(ErrorsContext);
  const { strategiesState } = useContext(StrategiesContext);

  const [stats, setStats] = useState({
    profits_in_usd: {
      funds_locked_in_active_deals: 0,
      today_usd_profit: 0,
      overall_usd_profit: 0,
    },
  });
  useEffect(() => {
    callApi("GET", `/public/api/ver1/bots/stats?bot_id=${botItem.id}`)
      .then((response) => response.json())
      .then((data) => {
        setStats(data);
      });
  }, []);

  const [maxUsage, setMaxUsage] = useState(0);
  useEffect(() => {
    getMaxUsage(botItem, account)
      .then((mxUsg) => {
        setMaxUsage(mxUsg);
      })
      .catch(() => {
        setMaxUsage(0);
        errorsDispatch({
          type: "addError",
          payload: `Error when fetching max usage for bot ${botItem.name}`,
        });
      });
  }, [account, botItem]);

  const [returns, setReturns] = useState(NaN);

  const age = differenceInDays(new Date(), new Date(botItem.created_at));
  const formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
  });

  const strategy = getBotsStrategyName(
    strategiesState,
    strategyMap,
    botItem.strategy_list
  );

  return (
    <div className="BotPreview">
      <div className="BotPreviewRow">
        <div className="BotPreviewIconsSetContainer">
          <IconsSet
            botType={botItem.type}
            strategy={botItem.strategy}
            botId={botItem.id}
            marketIcon={account.market_icon}
          />
          <BotInfo
            strategy={botItem.strategy}
            age={age}
            is_enabled={botItem.is_enabled}
          />
          <div className="BotReturns">
            <BotReturns
              returns={returns}
              setReturns={setReturns}
              maxUsage={maxUsage}
              botId={botItem.id}
            />
          </div>
        </div>
        <div className="BotPreviewTableContainer">
          <BotName name={botItem.name} />
          <Table
            striped
            size="sm"
            style={{ fontSize: "12px", marginBottom: "0" }}
          >
            <tbody>
              <tr>
                <td style={{ fontWeight: "bold", width: "170px" }}>Account</td>
                <td>{account.name}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold", width: "170px" }}>BotID</td>
                <td>{botItem.id}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Strategy</td>
                <td>{strategy}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Active deals/Max</td>
                <td>{`${botItem.active_deals_count}/${botItem.max_active_deals}`}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Today's profit/Loss</td>
                <td>
                  {formatter.format(stats.profits_in_usd.today_usd_profit)}
                </td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Total profit/Loss</td>
                <td>{formatter.format(botItem.finished_deals_profit_usd)}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Funds locked/Max usage</td>
                <td>{`${formatter.format(
                  getFundsLocked(
                    botItem,
                    stats.profits_in_usd.funds_locked_in_active_deals
                  )
                )}/${formatter.format(maxUsage)}`}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Current uPnL</td>
                <td>{formatter.format(botItem.active_deals_usd_profit)}</td>
              </tr>
            </tbody>
          </Table>
        </div>
      </div>
    </div>
  );
};

export const IconsSet = ({ botType, strategy, botId, marketIcon }: any) => (
  <div className="IconsSetContainer">
    <div className="BotTypeIconContainer">
      {botType === "Bot::SingleBot" ? "S" : "M"}
    </div>
    <div className="BotMarketIconContainer">
      <div
        className="BotMarketIcon"
        style={{
          backgroundImage: `url(${marketIcon})`,
        }}
      ></div>
    </div>
    <div className="RobotsIconContainer">
      <img
        src={`/robotsIcons/robots-vector-free-icon-set-${
          strategy === "short" ? "2" : "3"
        }${String(botId).slice(-1)}.png`}
        alt="logo"
        className="RobotsIcon"
      />
    </div>
  </div>
);
export const BotName = ({ name }: any) => (
  <div className="BotNameContainer">
    <span className="BotName">{name}</span>
  </div>
);

export const BotInfo = ({ strategy, age, is_enabled }: any) => (
  <div className="BotInfoContainer">
    <div className="BotInfoRecordContainer">
      <div className="BotInfoLabel">Side:</div>
      {strategy === "long" ? (
        <Badge bg="success">Long</Badge>
      ) : (
        <Badge bg="danger">Short</Badge>
      )}
    </div>
    <div className="BotInfoRecordContainer">
      <div className="BotInfoLabel">Age:</div>
      <div className="BotInfoValue">{`${age} days`}</div>
    </div>
    {!is_enabled && (
      <div className="BotInfoRecordContainer">
        <Badge bg="warning" text="dark">
          Disabled
        </Badge>
      </div>
    )}
  </div>
);

export const BotReturns = ({ returns, setReturns, maxUsage, botId }: any) => {
  const { errorsDispatch } = useContext(ErrorsContext);

  const formatter = new Intl.NumberFormat("en-US", {
    style: "percent",
    minimumFractionDigits: 2,
    signDisplay: "always",
  });
  const calculateReturns = async () => {
    try {
      const returns = await getReturns(maxUsage, botId);
      setReturns(returns);
    } catch {
      errorsDispatch({
        type: "addError",
        payload: "Error when fetching returns",
      });
      setReturns(NaN);
    }
  };

  return (
    <div className="BotReturnsContainer">
      {!isNaN(returns) && (
        <div
          className="BotReturnsValue"
          style={{ color: returns < 0 ? "#FF0000" : "#198754" }}
        >
          {`${formatter.format(returns || 0)}`}
        </div>
      )}
      <div className="BotReturnsLabel">Returns (30d)</div>
      {isNaN(returns) && (
        <Button
          onClick={() => {
            calculateReturns();
          }}
          variant="link"
          size="sm"
          active
        >
          Calculate
        </Button>
      )}
    </div>
  );
};
