import React, { useContext, useEffect } from "react";
import { FiltersContext, AccountsContext } from "../../App";
import Select from "react-select";
import "./BotsFilters.css";

const BotsFilters = () => {
  const { filtersState, filtersDispatch } = useContext(FiltersContext);
  const {
    accountsState: { accounts },
  } = useContext(AccountsContext);

  const accountsOptions = [
    { value: "", label: "All", label_id: "All" },
    ...accounts.map((account: any) => ({
      value: account.id,
      label: (
        <div className="AccountFilterItem">
          <div>
            <div
              className="AccountMarketIcon"
              style={{
                backgroundImage: `url(${account.market_icon})`,
              }}
            ></div>
          </div>
          <span>{account.name}</span>
        </div>
      ),
      label_id: account.name,
    })),
  ];

  const scopeOptions: any = [
    { value: "", label: "All" },
    { value: "enabled", label: "Enabled" },
    { value: "disabled", label: "Disabled" },
  ];

  const typeOptions: any = [
    { value: "", label: "All" },
    { value: "long", label: "Long" },
    { value: "short", label: "Short" },
  ];

  const sortingOptions: any = [
    { value: "profit-desc", label: "Profit (Highest First)" },
    { value: "profit-asc", label: "Profit (Lowest First)" },
    { value: "created_at-desc", label: "Age (Youngest first)" },
    { value: "created_at-asc", label: "Age (Oldest first)" },
  ];

  useEffect(() => {
    try {
      localStorage.setItem("botsFiltersState", JSON.stringify(filtersState));
    } catch {}
  }, [filtersState]);

  return (
    <div className="BotsFilters">
      <div className="BotsFilterGroup">
        <div className="FilterTitle">Account</div>
        <Select
          onChange={(option: any) => {
            filtersDispatch({ type: "setAccount", payload: option.value });
          }}
          value={accountsOptions.find(
            (option: any) => option.value === filtersState.account
          )}
          options={accountsOptions}
          className="AccountFilter"
          filterOption={(candidate, input) =>
            candidate.data.label_id.includes(input)
          }
        />
      </div>
      <div className="BotsFilterGroup">
        <div className="FilterTitle">Status</div>
        <Select
          onChange={(option: any) => {
            filtersDispatch({ type: "setScope", payload: option.value });
          }}
          value={scopeOptions.find(
            (option: any) => option.value === filtersState.scope
          )}
          options={scopeOptions}
          className="ScopeFilter"
        />
      </div>
      <div className="BotsFilterGroup">
        <div className="FilterTitle">Side</div>
        <Select
          onChange={(option: any) => {
            filtersDispatch({ type: "setType", payload: option.value });
          }}
          value={typeOptions.find(
            (option: any) => option.value === filtersState.type
          )}
          options={typeOptions}
          className="TypeFilter"
        />
      </div>
      <div className="BotsFilterGroup">
        <div className="FilterTitle">Sort by</div>
        <Select
          onChange={(option: any) => {
            filtersDispatch({ type: "setSorting", payload: option.value });
          }}
          value={sortingOptions.find(
            (option: any) => option.value === filtersState.sorting
          )}
          options={sortingOptions}
          className="Sorting"
        />
      </div>
    </div>
  );
};

export default BotsFilters;
