import React, { useContext, useEffect, useState } from "react";
import { getBots } from "../../utils/ApiUtils";
import { BotPreview } from "./BotPreview/BotPreview";
import InfiniteScroll from "react-infinite-scroll-component";
import "./BotsList.css";
import { FiltersContext, AccountsContext, ErrorsContext } from "../../App";
import { Spinner } from "react-bootstrap";

export const BotsList = () => {
  const {
    filtersState: { account, sorting, type, scope },
  } = useContext(FiltersContext);
  const {
    accountsState: { accounts },
  } = useContext(AccountsContext);
  const { errorsDispatch } = useContext(ErrorsContext);

  const limit = 10;
  const [bots, setBots] = useState<Array<any>>([]);
  const [offset, setOffset] = useState(0);
  const [hasMore, setHasMore] = useState(true);

  const getAccountByBotId = (accountId: any) =>
    accounts.find((account: any) => account.id === accountId);

  const getMoreBoats = async (resetOffset: boolean = false) => {
    setHasMore(true);
    try {
      const data = await getBots(
        offset,
        limit,
        sorting,
        scope,
        type,
        account,
        resetOffset
      );

      if (data.length < limit) {
        setHasMore(false);
      }
      if (resetOffset) {
        setOffset(limit);
        setBots(data);
      } else {
        setOffset((os) => os + limit);
        setBots((bots) => [...bots, ...data]);
      }
    } catch {
      setBots([]);
      setOffset(limit);
      setHasMore(false);
      errorsDispatch({
        type: "addError",
        payload: "Error when fetching bots list",
      });
    }
  };

  useEffect(() => {
    getMoreBoats(true);
  }, [account, sorting, type, scope]);

  const isNothingFound = !hasMore && bots.length === 0;

  return isNothingFound ? (
    <div className="FallbackContainer">
      <p style={{ textAlign: "center" }}>
        <b>No results found =(</b>
      </p>
    </div>
  ) : (
    <InfiniteScroll
      dataLength={bots.length}
      next={async () => {
        await getMoreBoats();
      }}
      hasMore={hasMore}
      loader={
        <div className="SpinnerContainer">
          <Spinner animation="grow" />
        </div>
      }
      endMessage={
        <p style={{ textAlign: "center" }}>
          <b>Yay! You have seen it all</b>
        </p>
      }
    >
      <div className="BotsList">
        {accounts.length > 0 &&
          bots.map((bot: any) => (
            <BotPreview
              botItem={bot}
              key={bot.id}
              account={getAccountByBotId(bot.account_id)}
            />
          ))}
      </div>
    </InfiniteScroll>
  );
};
